                that.minute +=1;
              }
              if(that.minute==60){
                that.minute =0;
                that.hour +=1;
              }
              if(that.hour==24){
                that.minute =0;
                that.hour =0;
                that.second =0;
              }
              if(that.second >=10){
                that.secHide =false;
              }else{
                that.secHide =true;
              }
              if(that.minute >=10){
                that.minHide =false;
              }else{
