<template>
	<div>
		<h1>{{time}}</h1>
	</div>
</template>
<script>
   export default {
		data(){
			return{
				timer:"",
				time:"00:00:00"
			}
		},
		mounted(){
			var h =0;
			var m =0;
			var s =0;
			var that =this;
			this.second();

		},
		methods:{
		    second(){
				var h =0;
				var m =0;
				var s =0
				this.timer=setInterval(function(){
					s++;
					if(s>0 && (s%60)==0){m+=1;s=0;}  
					if(m>0 && (m%60)==0){h+=1;m=0;}  
					
					this.time=h+":"+m+":"+s;

				},1000)
				
				
			} 
		}
	}
</script>



