import Vue from 'vue'
import Router from 'vue-router'

import Staging from '@/components/call-center/staging.vue'
import Call from '@/components/call-center/call.vue'
import Notice from '@/components/call-center/notice.vue'
import WorkOrder from '@/components/call-center/workOrder.vue'
import Knowledge from '@/components/call-center/knowledge.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Staging',
      component: Staging
    },
    {
      path: '/call',
      name: 'Call',
      component: Call
    },
    {
      path: '/notice',
      name: 'Notice',
      component: Notice
    },
    {
      path: '/workOrder',
      name: 'WorkOrder',
      component: WorkOrder
    },
    {
      path: '/knowledge',
      name: 'Knowledge',
      component: Knowledge
    }
  ]
})



